<template>
  <v-card>
    <v-card-title>
      <v-row no-gutters>
        <v-col cols="2">
          <v-row justify="center">
            <v-btn
              color="#c98b3e"
              class="white--text"
              fab
              small
              bottom
              @click="openCreateDialog"
            >
              <v-icon>mdi-plus</v-icon>
            </v-btn>
          </v-row>
        </v-col>
        <v-col cols="8"> Tarjetas de pre-pago </v-col>
      </v-row>
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        :label="searchLabel"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="cardList"
      :search="search"
      :loading="cardList.length == 0"
      loading-text="Cargando...Porfavor espere"
    >
      <template v-slot:[`item.createdAt`]="{ item }">
        <span>
          {{ new Date(item.createdAt).toLocaleDateString() }}
        </span>
      </template>
      <template v-slot:[`item.updatedAt`]="{ item }">
        <span>
          {{
            item.updatedAt ? new Date(item.updatedAt).toLocaleDateString() : ""
          }}
        </span>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
            <v-icon
              small
              class="mr-2"
              v-on="on"
              v-bind="attrs"
              @click="updateItem(item)"
            >
              mdi-pencil
            </v-icon>
          </template>
          <span>Editar</span>
        </v-tooltip>
        <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
            <v-icon
              small
              class="mr-2"
              @click="reloadItem(item)"
              v-on="on"
              v-bind="attrs"
            >
              mdi-autorenew
            </v-icon>
          </template>
          <span>Recargar</span>
        </v-tooltip>
        <!--- <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
            <v-icon small class="mr-2" v-on="on" v-bind="attrs"> mdi-timeline-text </v-icon>
          </template>
          <span>Historial</span>
        </v-tooltip> --->
        <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
            <v-icon
              small
              class="mr-2"
              @click="deleteItem(item)"
              v-on="on"
              v-bind="attrs"
              color="red lighten-2"
            >
              mdi-delete
            </v-icon>
          </template>
          <span>Eliminar</span>
        </v-tooltip>
      </template>
    </v-data-table>
    <create-card></create-card>
    <update-card></update-card>
    <reload-card></reload-card>
    <DeleteDialog :item="itemToDelete"></DeleteDialog>
  </v-card>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import CreateDialog from "@/components/CreateDialog.vue";
import DeleteDialog from "@/components/DeleteDialog.vue";
import UpdateDialog from "@/components/UpdateDialog.vue";
import ReloadDialog from "@/components/ReloadDialog.vue";

export default {
  name: "Home",
  components: {
    "create-card": CreateDialog,
    "update-card": UpdateDialog,
    "reload-card": ReloadDialog,
    DeleteDialog,
  },
  data: () => ({
    searchLabel: "Coloque la tarjeta en el lector...",
    search: "",
    itemToDelete: {
      id: "",
      cardId: "",
    },
    headers: [
      { text: "Tarjeta", value: "cardId" },
      {
        text: "Nombre",
        align: "start",
        value: "owner.name",
      },
      { text: "Telfono", value: "owner.phone" },
      {
        text: "ltimo paquete adquirido",
        align: "center",
        value: "pack",
      },
      {
        text: "Disponible",
        align: "center",
        value: "available",
      },
      { text: "Fecha de creado", value: "createdAt" },
      { text: "Fecha de actualizado", value: "updatedAt" },
      { text: "Opciones", value: "actions" },
    ],
  }),
  mounted() {
    this.$store.dispatch("getCards");
    this.$store.dispatch("getPacks");
  },
  methods: {
    ...mapMutations([
      "changeCreateDialog",
      "changeDeleteDialog",
      "changeUpdateDialog",
      "changeReloadDialog",
      "CARD_TO_UPDATE",
    ]),
    deleteItem({ id, cardId }) {
      this.itemToDelete.id = id;
      this.itemToDelete.cardId = cardId;
      this.changeDeleteDialog({ dialog: true });
    },
    openCreateDialog() {
      this.changeCreateDialog({ dialog: true });
    },
    updateItem(data) {
      const {
        id,
        cardId,
        available,
        pack,
        owner,
        logs,
        createdAt,
        updatedAt,
      } = data;
      const itemToupdate = {
        id,
        cardId,
        available,
        pack,
        owner: owner
          ? { id: owner.id, name: owner.name, phone: owner.phone }
          : { id: "", name: "", phone: "" },
        logs: logs ? logs : [],
        createdAt,
        updatedAt,
      };

      this.CARD_TO_UPDATE(itemToupdate);
      this.changeUpdateDialog({ dialog: true });
    },
    reloadItem(data) {
      const {
        id,
        cardId,
        available,
        pack,
        owner,
        logs,
        createdAt,
        updatedAt,
      } = data;
      const itemToupdate = {
        id,
        cardId,
        available,
        pack,
        owner: owner
          ? { id: owner.id, name: owner.name, phone: owner.phone }
          : { id: "", name: "", phone: "" },
        logs: logs ? logs : [],
        createdAt,
        updatedAt,
      };

      this.CARD_TO_UPDATE(itemToupdate);
      this.changeReloadDialog({ dialog: true });
    },
  },
  computed: {
    ...mapGetters(["cardList"]),
  },
};
</script>
