<template>
  <v-dialog v-model="isOpenReload" persistent max-width="450">
    <v-card>
      <v-card-title class="headline"> Recargar Tarjeta </v-card-title>
      <v-card-text>
        <v-container>
          <v-form v-model="isValid" lazy-validation>
            <v-row>
              <v-col cols="12">
                <v-text-field
                  v-model="cardToUpdate.cardId"
                  label="Num. de tarjeta"
                  disabled
                ></v-text-field>
              </v-col>
              <v-col cols="12"> disponibles: {{ newTotal }} </v-col>
              <v-col cols="12">
                <v-select
                  v-model="packId"
                  :items="packList"
                  item-text="quantity"
                  item-value="id"
                  label="Paquete"
                  clearable
                ></v-select>
              </v-col>
              <v-col cols="12">
                <v-list>
                  <v-list-group prepend-icon="mdi-timeline-text" no-action>
                    <template v-slot:activator>
                      <v-list-item-content>
                        <v-list-item-title
                          v-text="'Historial de transacciones'"
                        ></v-list-item-title>
                      </v-list-item-content>
                    </template>
                    <v-list-item
                      v-for="child in cardToUpdate.logs"
                      :key="child.id"
                    >
                      <v-list-item-content>
                        <v-list-item-title>{{
                          child.type === "ADD" ? "Recarga" : "Retiro"
                        }}</v-list-item-title>
                        <v-list-item-subtitle
                          >Se realiz:
                          {{
                            new Date(child.createdAt).toLocaleDateString()
                          }}</v-list-item-subtitle
                        >
                        <v-list-item-subtitle
                          >Monto cargado:
                          {{ child.amount }}</v-list-item-subtitle
                        >
                        <v-list-item-subtitle
                          >Monto anterior:
                          {{ child.amountBefore }}</v-list-item-subtitle
                        >
                        <v-list-item-subtitle
                          >Monto actual:
                          {{ child.amountAfter }}</v-list-item-subtitle
                        >
                      </v-list-item-content>
                    </v-list-item>
                  </v-list-group>
                </v-list>
              </v-col>
            </v-row>
          </v-form>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="red darken-1" text @click="closeDialogHandler(false)">
          Cancelar
        </v-btn>
        <v-btn color="green darken-1" text @click="saveDialogHandler(false)">
          Guardar
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

export default {
  name: "ReloadDialog",
  data: () => ({
    isValid: true,
    packId: "",
  }),
  methods: {
    ...mapMutations(["changeReloadDialog", "CARD_TO_UPDATE"]),
    closeDialogHandler(dialog) {
      this.changeReloadDialog({ dialog });
      this.packId = "";
      this.CARD_TO_UPDATE({
        id: "",
        cardId: "",
        available: 0,
        pack: 0,
        owner: { id: "", name: "", phone: "" },
        logs: [],
        createdAt: "",
        updatedAt: "",
      });
    },
    async saveDialogHandler(dialog) {
      this.$store
        .dispatch("reloadCard", {
          cardId: this.cardToUpdate.cardId,
          packId: this.packId,
        })
        .finally(() => {
          this.packId = "";
          this.CARD_TO_UPDATE({
            id: "",
            cardId: "",
            available: 0,
            pack: 0,
            owner: { id: "", name: "", phone: "" },
            logs: [],
            createdAt: "",
            updatedAt: "",
          });
          this.changeReloadDialog({ dialog });
        });
    },
  },
  computed: {
    ...mapGetters(["isOpenReload", "packList", "cardToUpdate"]),
    newTotal() {
      if (this.packId && this.packId !== "") {
        const { equalsTo } = this.packList.find(({ id }) => id === this.packId);
        return this.cardToUpdate.available + equalsTo;
      }
      return this.cardToUpdate.available;
    },
  },
};
</script>

