<template>
  <v-dialog v-model="isOpenUpdate" persistent max-width="450">
    <v-card>
      <v-card-title class="headline"> Actualizar Tarjeta </v-card-title>
      <v-card-text>
        <v-container>
          <v-form ref="formUpdateCard" v-model="isValid" lazy-validation>
            <v-row>
              <v-col cols="12">
                <v-text-field
                  v-model="cardToUpdate.owner.name"
                  :rules="[rules.noValidChars, rules.noNumbers]"
                  label="Nombre del propietario"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  v-model="cardToUpdate.owner.phone"
                  :rules="[rules.phone]"
                  label="Telfono"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  v-model="cardToUpdate.cardId"
                  label="Num. de tarjeta"
                  disabled
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                disponibles: {{ cardToUpdate.available }}
              </v-col>
              <v-col cols="12">
                <v-list>
                  <v-list-group prepend-icon="mdi-timeline-text" no-action>
                    <template v-slot:activator>
                      <v-list-item-content>
                        <v-list-item-title
                          v-text="'Historial de transacciones'"
                        ></v-list-item-title>
                      </v-list-item-content>
                    </template>
                    <v-list-item
                      v-for="child in cardToUpdate.logs"
                      :key="child.id"
                    >
                      <v-list-item-content>
                        <v-list-item-title>{{
                          child.type === "ADD" ? "Recarga" : "Retiro"
                        }}</v-list-item-title>
                        <v-list-item-subtitle
                          >Se realiz:
                          {{
                            new Date(child.createdAt).toLocaleDateString()
                          }}</v-list-item-subtitle
                        >
                        <v-list-item-subtitle
                          >Monto cargado:
                          {{ child.amount }}</v-list-item-subtitle
                        >
                        <v-list-item-subtitle
                          >Monto anterior:
                          {{ child.amountBefore }}</v-list-item-subtitle
                        >
                        <v-list-item-subtitle
                          >Monto actual:
                          {{ child.amountAfter }}</v-list-item-subtitle
                        >
                      </v-list-item-content>
                    </v-list-item>
                  </v-list-group>
                </v-list>
              </v-col>
            </v-row>
          </v-form>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="red darken-1" text @click="closeDialogHandler(false)">
          Cancelar
        </v-btn>
        <v-btn color="green darken-1" text @click="saveDialogHandler(false)">
          Guardar
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

export default {
  name: "UpdateDialog",
  data: () => ({
    isValid: true,
    rules: {
      required: (value) => !!value || "Obligatorio.",
      phone: (v) => !v || new RegExp(/^[0-9]+$/).test(v) || "Telfono invlido",
      noValidChars: (v) =>
        new RegExp(
          /^[^`~!@"#$%^&*()_+={}[\]|\\:;<,>?\-']*$/g
        ).test(v) || "Carcteres no vlidos.",
      noNumbers: (v) =>
        new RegExp(/^([^0-9]*)$/g).test(v) || "No debes escribir nmeros.",
    },
  }),
  methods: {
    ...mapMutations(["changeUpdateDialog", "CARD_TO_UPDATE"]),
    closeDialogHandler(dialog) {
      this.changeUpdateDialog({ dialog });
      this.CARD_TO_UPDATE({
        id: "",
        cardId: "",
        available: 0,
        pack: 0,
        owner: { id: "", name: "", phone: "" },
        logs: [],
        createdAt: "",
        updatedAt: "",
      });
      this.$refs.formUpdateCard.resetValidation();
    },
    async saveDialogHandler(dialog) {
      const isValidForm = this.$refs.formUpdateCard.validate();
      if (isValidForm) {
        this.$store
          .dispatch("ownerCard", {
            cardId: this.cardToUpdate.cardId,
            name: this.cardToUpdate.owner ? this.cardToUpdate.owner.name : "",
            phone: this.cardToUpdate.owner ? this.cardToUpdate.owner.phone : "",
          })
          .finally(() => {
            this.CARD_TO_UPDATE({
              id: "",
              cardId: "",
              available: 0,
              pack: 0,
              owner: { id: "", name: "", phone: "" },
              logs: [],
              createdAt: "",
              updatedAt: "",
            });
            this.changeUpdateDialog({ dialog });
          });
      }
    },
  },
  computed: {
    ...mapGetters(["isOpenUpdate", "packList", "cardToUpdate"]),
  },
};
</script>

