<template>
  <v-dialog v-model="isOpenCreate" persistent max-width="290">
    <v-card>
      <v-card-title class="headline"> Nueva Tarjeta </v-card-title>
      <v-card-text>
        <v-container>
          <v-form ref="formCreateCard" v-model="isValid" lazy-validation>
            <v-row>
              <v-col cols="12">
                <v-text-field
                  v-model="name"
                  :rules="[rules.noValidChars, rules.noNumbers]"
                  label="Nombre del propietario"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  v-model="phone"
                  :rules="[rules.phone]"
                  label="Telfono"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  v-model="cardId"
                  :rules="[rules.required]"
                  label="Num. de tarjeta"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-select
                  v-model="packId"
                  :items="packList"
                  :rules="[rules.required]"
                  item-text="quantity"
                  item-value="id"
                  label="Paquete"
                  clearable
                ></v-select>
              </v-col>
            </v-row>
          </v-form>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="red darken-1" text @click="closeDialogHandler(false)">
          Cancelar
        </v-btn>
        <v-btn color="green darken-1" text @click="saveDialogHandler(false)">
          Guardar
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

export default {
  name: "CreateDialog",
  data: () => ({
    isValid: true,
    name: null,
    phone: null,
    cardId: "",
    packId: "",
    rules: {
      required: (value) => !!value || "Obligatorio.",
      phone: (v) => ( !v || new RegExp(/^[0-9]+$/).test(v) ) || 'Telfono invlido',
      noValidChars: (v) => new RegExp(/^[^`~!@"#$%^&*()_+={}[\]|\\:;<,>?\-']*$/g).test( v ) || 'Carcteres no vlidos.',
      noNumbers: (v) => new RegExp(/^([^0-9]*)$/g).test(v) || 'No debes escribir nmeros.',
    },
  }),
  methods: {
    ...mapMutations(["changeCreateDialog"]),
    closeDialogHandler(dialog) {
      this.changeCreateDialog({ dialog });
      this.cardId = "";
      this.packId = "";
      this.name = null;
      this.phone = null;
      this.$refs.formCreateCard.resetValidation();
    },
    async saveDialogHandler(dialog) {
      const isValidForm = this.$refs.formCreateCard.validate();
      let owner = {};
      if(this.name) owner.name = this.name;
      if(this.phone) owner.phone = this.phone;
      if (isValidForm) {
        this.$store
          .dispatch("addCard", 
          { 
            owner: {...owner},
            cardId: this.cardId, 
            packId: this.packId,
          })
          .finally(() => {
            this.closeDialogHandler(dialog);
          });
      }
    },
  },
  computed: {
    ...mapGetters(["isOpenCreate", "packList"]),
  },
};
</script>

